.scheduling-container {
    /* height: 100vh; */
    /* width: 100%; */
    margin-top: 40px;
    margin-bottom: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: insert 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
} 
.scheduling-container h1 {
    color: #777;
    display: block;
    font-weight: 300;
    margin-bottom: 10px;
    padding-bottom: 1rem;
}

#contact-container {
    padding-top: 2rem;
    align-items: center;
}

#contact-container p {
    color: #777;
    text-align: center;
    font-size: 26px;
    line-height: 36px;
}

#contact-container a {
    color: darkgray;
    display: inline;
    text-decoration: none;
    height: 100%;
}

#contact-container a:hover {
    color: #242424;
    border-bottom: 4px solid #242424;
    transition: all 0.2s ease-out;
}

.scheduling-container .btn--outline {
    color: darkgray;
    border: 1px solid orange
}

.scheduling-container .btn--outline:hover {
    background-color: #242424;
    border: 1px solid black;
    color: #fff;
}
